@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap);
#github, #behance, #instagram, #linkedin{
    padding: 5px;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.3s;
}

#github:hover, #behance:hover, #instagram:hover, #linkedin:hover{
    background: rgba( 255, 255, 255, 0.2 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 4px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );

    cursor: pointer;
    transition: 0.3s;
}
nav {
    z-index: 20;
    position: fixed;
    width: 100vw;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;

    background: rgba( 255, 255, 255, 0.2 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 5px );
    -webkit-backdrop-filter: blur( 5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}
nav ul {
    color: #F3F3F3;
    font-weight: 600;
}
.icons a:-webkit-any-link{
    color: transparent;
}
/* .icons svg{
    width: 80px;
} */
#logo{
    margin-top: -6px;
}
.algin-center-nav{
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 30px;
    gap: 30px;
}
.text-nav li a{
    color: #F3F3F3;
    display: inline-block;
    position: relative;
    
    margin: 15px;
    font-size: 1.1rem;  

    cursor: pointer;
    transition: 0.5s;
}
/* effect-underline */
.text-nav li a:after {
	content: '';
    position: absolute;
    display: inline-block;
    left: 0;
    height: 1em;
    width: 100%;
    border-bottom: 1px solid;
    margin-top: 10px;
    opacity: 0;
	transition: opacity 0.35s, transform 0.35s;
	transform: scale(0,1);
}
.text-nav li a:hover:after {
    opacity: 1;
	transform: scale(1);
}
@media (max-width: 768px){
    .text-nav li a{
        font-size: 0.9rem;
    }
    #logo{
        width: 7rem;
        height: 7rem;
    }
    .algin-center-nav{
        grid-gap: 10px;
        gap: 10px;
    }
}
@media (max-width: 426px){
    .text-nav li a{
        font-size: 0.8rem;
    }
    .algin-center-nav{
        grid-gap: 15px;
        gap: 15px;
    }
    .iconsMobile{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        grid-gap: 15px;
        gap: 15px;
        width: 100%;
    }
    .iconsMobile svg{
        width: 2rem;
    }
    #logo{
        width : 5.3rem;
        height: 5.3rem;
    }
    nav{
        bottom: 0;
    }
}
.frase-imagens{
    background: linear-gradient(to right, #E0830A , #FF960C);
    height: 100vh;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.frase-imagens h1 {
    font-size: 7rem;
    text-align: center;
    font-weight: 700;

    width: 50%;
    color: #C37815;
}
.frase-imagens span {
    color: #FEE7C9;
}
.frase-imagens button {
    margin-top: 30px;
    padding: 10px 40px;

    background-color: #FEE7C9;
    border: 2.3px solid #FEE7C9;
    border-radius: 5px;
    color: #E0830A;

    font-size: 1.3rem;
    font-weight: 600;
    cursor: pointer;

    transition: 0.3s;
}
.frase-imagens button:hover {
    background-color: transparent;
    border: 2.3px solid #FEE7C9;
    border-radius: 5px;
    color: #FEE7C9;

    transition: 0.3s;
}

/* Laptop */
@media(max-width: 1024px){
    .frase-imagens h1 {
        font-size: 5.5rem;
    }
}
/* Tablet */
@media(max-width: 768px){
    .frase-imagens h1 {
        font-size: 4.5rem;
        width: 60%;
    }
}
/* Mobile */
@media(max-width: 426px){
    .frase-imagens{
        height: 125vh;
    }
    .frase-imagens h1 {
        font-size: 3.5rem;
        width: 80%;
    }
    .frase-imagens button{
        font-size: 1.1rem;
        padding: 10px 20px;
        margin: 20px 0 0 0;
    }
}
.portfolio{
    margin: 0 auto;
    display: grid;
    
    margin: 50px;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}
.portfolio button {
    border: none;
    background-color: transparent;
}
.portfolio img, .portfolio div{
    border-radius: 10px;
    cursor: pointer;
    width: 100%;
    
    border: 3px solid rgba(224, 131, 10, 0);
    transition: 0.3s;
}
.portfolio img:hover, .portfolio div:hover{
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    border: 3px solid #E0830A;
    border-radius: 16px;

    cursor: pointer;
    transition: 0.3s;
}
.portfolio div{
    background-color: #FEE7C9;

    display: flex;
    justify-content: center;
    align-items: center;
}
.portfolio div svg{
    width: 62px;
    height: 62px;
}

/* Modal */
.portifolio-open{
    justify-content: center;
    align-items: center;
}
.portifolio-open div {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.portifolio-open img {
    width: 100%;
}
.portifolio-open button {
    background-color: #E0830A;
    border: 1px solid #E0830A;
    border-radius: 5px;

    color: #f3f3f3;
    font-size: 1rem;
    cursor: pointer;

    margin: 20px 0;
    padding: 10px 20px;

    transition: 0.3s;
}
.portifolio-open a{color: #f3f3f3;}
.portifolio-open button:hover{
    -webkit-filter: brightness(0.9);
            filter: brightness(0.9);
    transition: 0.3s;
}
.ReactModal__Overlay{
    z-index: 21;
    background-color: rgba(0, 0, 0, 0.5) !important;
}
#modalDelete{
    width: 90vw;
    height: 90vh;
    margin: 0 auto;
    padding: 0 !important;
}
.closeBtn {
    background-color: #E0830A;
    border: 1px solid #E0830A;
    
    color: #f3f3f3;
    font-size: 0.9rem;
    font-weight: 600;

    width: 100%;
    padding: 7px 20px;
    cursor: pointer;
    transition: 0.3s;
}
.closeBtn:hover{
    -webkit-filter: brightness(0.9);
            filter: brightness(0.9);
    transition: 0.3s;
}

@media(max-width: 426px){
    .portfolio{
        grid-template-columns: repeat(1, 1fr);
    }
    .portifolio-open button{
        padding: 8px 15px;
        font-size: 0.9rem;
    }
    #modalDelete{
        width: 95vw;
        height: 95vh;
    }
    .ReactModal__Content {
        inset: 20px 35px 0 0 !important;
    }
}
#Chat, #buttonChat{
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    cursor: pointer;

    bottom: 10px;
    right: 10px;
    width: 52px;
    height: 52px;
    padding: 7px;

    fill: white;
    background-color: #C37815;
    border: 2px solid #C37815;
    border-radius: 30%;
    opacity: 1;
}

/* Lista das perguntas frequentes */
.list-questions {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    margin: 50px 0;
}
.questions, .duvida{
    max-width: 550px;
}
.questions {
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    grid-gap: 35px;
    gap: 35px;
}

.questions button, #modalQuestion button {
    display: flex;
    justify-content: space-around;
    align-items: center;

    background: linear-gradient(to right, #E0830A , #FF960C);
    border: none;

    color: #f3f3f3;
    font-weight: 400;
    font-size: 1rem;

    cursor: pointer;
}
.questions button {
    width: 85%;
    height: 20%;
    border-radius: 10px;
    padding: 10px 20px;
}
.enviar-duvida {
    background: linear-gradient(to right, #E0830A , #FF960C);
    border: 3px solid linear-gradient(to right, #E0830A , #FF960C);
    color: #f3f3f3;

    border-radius: 7px;
    padding: 15px;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.enviar-duvida h3{
    font-weight: 500;
    font-size: 1.2rem;
    text-align: left;
}
.enviar-duvida p{
    font-weight: 300;
    text-align: left;
    font-size: 1rem;

    margin: 5px 0 20px 0;
}
.enviar-duvida svg{
    margin-top: -15px;
}
.enviar-duvida textarea, .enviar-duvida input{
    background-color: #C37815;
    color: #f3f3f3;

    width: 100%;
    height: 100%;
    padding: 10px;
    border: 2px solid #C37815;
    border-radius: 5px;
}

.enviar-duvida input {
    margin: 10px 0;
    
}
.enviar-duvida input::-webkit-input-placeholder, .enviar-duvida textarea::-webkit-input-placeholder{color: #fee7c9;}
.enviar-duvida input:-ms-input-placeholder, .enviar-duvida textarea:-ms-input-placeholder{color: #fee7c9;}
.enviar-duvida input::placeholder, .enviar-duvida textarea::placeholder{color: #fee7c9;}
.enviar-duvida textarea:focus, .enviar-duvida input:focus{
    overflow: auto;
    outline: none;
    resize: none;

    border: 2px solid #E3AA5F;
}
.enviar-duvida button {
    background-color: #fee7c9;
    border: 1px solid #fee7c9;
    border-radius: 5px;

    color: #C37815;
    font-size: 0.9rem;
    text-transform: uppercase;
    font-weight: 500;
    
    cursor: pointer;
    width: 100%;
    padding: 8px;

    transition: 0.3s;
}
.enviar-duvida button:hover{
    -webkit-filter: brightness(0.9);
            filter: brightness(0.9);
    transition: 0.3s;
}
.ReactModal__Overlay {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -60px;

    z-index: 21;
    background-color: rgba(0, 0, 0, 0.5) !important;
}
/* Modal */
#modalQuestion {
    width: 40vw;
    height: 35vh;
    border-radius: 10px;

    margin: 10% auto;
    align-items: center;
    padding: 0 !important;

    animation: flip 1s;
}
#modalQuestion button {
    width: 100%;
    height: 100%;
    padding: 0;
}
#modalQuestion button p {
    width: 80%;
    text-align: justify;
    margin: 20px 0;
}


@keyframes flip {
    from{
        transform: perspective(2500px) rotateX(100deg);
    }
    to{
        transform: perspective(2500px) rotateX(0);
    }
}

/* Mobile */
@media(max-width: 426px){
    .enviar-duvida{
        width: 85%;
        margin: 0 auto;
    }
    #modalQuestion {
        position: relative !important;
        width: 90vw;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }
    #modalQuestion button {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
/* Entarda */
.introducao{
