    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: center;
}
#developer, #design{
    font-size: 13.2rem;
    font-weight: 700;
    letter-spacing: 1rem;
    text-transform: uppercase;
    width: 100%;
    height: 31%;
}
#developer      {color: #C37815;}
#design         {color: #E3AA5F;}
#developer span {color: #E3AA5F;}
#design    span {color: #C37815;}


.aboutMe{
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    padding: 30px 0;
    background: linear-gradient(to right, #E0830A , #FF960C);
}
.aboutMe img{
    width: 35%;
    max-width: 400px;
    height: 35%;
    max-height: 450px;

    border-radius: 2px;
    border: 2px solid #C37815;
    box-shadow: 2px 2px 25px #E3AA5F;
