    font-weight: 700;
    letter-spacing: 5px;
    font-size:6rem;
    text-align: left;

    margin-bottom: 5px;
}
.aboutMe span{
    color: #ffd59f;
}
.aboutMe p{
    color: #ffd59f;
    width: 96%;
    justify-content: center;
    font-weight: 500;
    font-size: 1rem;
    padding: 10px;
    text-align: justify;
}
.aboutMe a{
    color: #C37815;
    background-color: #ffd59f;
    padding: 0 10px;
}

/* Laptop */
@media(max-width: 1024px){
    .aboutMe h2{
        font-size: 4em;
    }
    #developer, #design{
        font-size: 9.2rem;
    }
}
/* Tablet */
@media(max-width: 768px){
