        width:50%;
    }
    .aboutMe p{
        font-size: 0.9rem;
        width: 89%;
    }
    #developer, #design{
        font-size: 6.5rem;
        height: 20%;
    }
}
/* Mobile */
@media(max-width: 426px){
    .introducao{
        height: 100vh;
    
        margin: -30px 0 0 0;
        display: flex;
        flex-direction: column;
    }
    .logoMobile{
        content: url(/Portfolio/static/media/Logo.9a11f37e.png);
        margin: 0 auto;
        width: 8rem;
        height: 8rem;
    }
    #developer, #design{
        font-size: 4.3rem;
        height: 30%;
        font-weight: 800;
    }
    #developer span, #design span{
        display: flex;
        flex-direction: column;
    }
    #developer span {color: #C37815;}
    #design    span {color: #E3AA5F;}
    .aboutMe{
        padding-top: 100px;
        flex-direction: column;
    }   
    .aboutMe img{
        justify-content: flex-start;
    } 
    .aboutMe div{
        width: 90%;
    }
    .aboutMe h2{
        font-size: 3.5em;
    }
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
}

html{
    scroll-behavior: smooth;
    max-width: 100%;
    overflow-x: hidden;
}

a{
    text-decoration: none;
}
a:hover{
    text-decoration: none;
}

li {
    list-style-type: none;
}

/* Scrol */
::-webkit-scrollbar{
    width: 15px;
    background-color: #E0830A;
}
::-webkit-scrollbar-thumb{
    background: rgba( 255, 255, 255, 0.5 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 5px );
    -webkit-backdrop-filter: blur( 5px );
    border-radius: 5px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}
