.frase-imagens{
    background: linear-gradient(to right, #E0830A , #FF960C);
    height: 100vh;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.frase-imagens h1 {
    font-size: 7rem;
    text-align: center;
    font-weight: 700;

    width: 50%;
    color: #C37815;
}
.frase-imagens span {
    color: #FEE7C9;
}
.frase-imagens button {
    margin-top: 30px;
    padding: 10px 40px;

    background-color: #FEE7C9;
    border: 2.3px solid #FEE7C9;
    border-radius: 5px;
    color: #E0830A;

    font-size: 1.3rem;
    font-weight: 600;
    cursor: pointer;

    transition: 0.3s;
}
.frase-imagens button:hover {
    background-color: transparent;
    border: 2.3px solid #FEE7C9;
    border-radius: 5px;
    color: #FEE7C9;

    transition: 0.3s;
}

/* Laptop */
@media(max-width: 1024px){
    .frase-imagens h1 {
        font-size: 5.5rem;
    }
}
/* Tablet */
@media(max-width: 768px){
    .frase-imagens h1 {
        font-size: 4.5rem;
        width: 60%;
    }
}
/* Mobile */
@media(max-width: 426px){
    .frase-imagens{
        height: 125vh;
    }
    .frase-imagens h1 {
        font-size: 3.5rem;
        width: 80%;
    }
    .frase-imagens button{
        font-size: 1.1rem;
        padding: 10px 20px;
        margin: 20px 0 0 0;
    }
}